.TodoInsert {
  display: flex; 
  background: #495057;
  input {
    background: none;
    outline: none;
    border: none;
    padding: .5rem;
    font-size: 1.125rem; line-height: 1.5;
    color: #fff; 
    &::placeholder {
      color: #dee2e6;
    }
    flex: 1;
  }
  button {
    background: none;
    outline: none; border: none;
    background: #868e96;
    color: #fff; padding: 0 1rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    cursor: pointer; transition: .1s background ease-in;
    &:hover {
      background: #adb5bd;
    }
  }
}